<!--  -->
<template>
  <div class="channel">
    <van-grid :column-num="2" class="vanBox">
      <van-grid-item
        v-for="(item, index) in channelList.data"
        :key="index"
        icon="photo-o"
        text="文字"
        class="vanItem"
        @click="gotoList(index)"
      >
        <img :src="item.icon" :alt="item.catename" />
        <h5>{{ item.catename }}</h5>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import Vue from "vue";
import { Grid, GridItem } from "vant";

Vue.use(Grid);
Vue.use(GridItem);
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      channelList: [],
      listId: ""
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    getChannelList: function() {
      this.axios
        .get("/apiv3/cate/getList")
        .then(res => {
          // console.log(res);
          this.channelList = res.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    gotoList: function(a) {
      console.log(a);
      // if(this.channelList.data[a].cateid){
      // this.listId = this.channelList.data[a].cateid

      //   }else{
      //     this.listId = 1
      // }
      this.listId = this.channelList.data[a].cateid;
      // console.log(this.channelList.data[a].cateid);

      this.$router.push({ name: "listShow", params: { listId: this.listId } });
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getChannelList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang="less" scoped>
//@import url(); 引入公共css类
.channel {
  .vanBox {
    .vanItem {
      position: relative;
      // height: 187px;
      overflow: hidden;
      img {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 187px;
      }
      h5 {
        z-index: 10;
        font-size: 26px;
        color: white;
        font-weight: 700;
      }
    }
  }
}
</style>
