<!-- 个人页面 -->
<template>
  <div class="serch" :style="height">
    <div class="serchBox">
      <!-- <input type="text" class=""> -->
      <span class="iconfont serchicon">&#xe688;</span>
      <input type="text" placeholder="请输入要搜索的内容" @change="goserch()" v-model="inputvalue" />
      <button @click="hide()">取消</button>
    </div>
    <div class="wordsBox" v-if="wordsShow">
      <h4>热门搜索</h4>
      <ul>
        <li v-for="(item,index) in itemlist" :key="index" @click="gotoSerch(item.kw)">{{item.kw}}</li>
      </ul>
    </div>
    <div class="answerBox" v-if="answerShow">
      <ul v-if="answer">
        <li v-for="(item,index) in answer.list" :key="index" @click="goVideo(item.postid)">{{item.title}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      height: {
        height: ""
      },
      itemlist: [],
      answer: [],
      inputvalue: "",
      wordsShow: true,
      answerShow: false
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    getheigth: function() {
      // console.log(window.innerHeight);

      this.height.height = window.innerHeight + "px";
    },
    getitemlist: function() {
      this.axios
        .get(`/apiv3/search?kw=`)
        .then(res => {
          // console.log(res);

          this.itemlist = res.data.data.recommend_keywords;
        })
        .catch(error => {
          console.log(error);
        });
    },
    goserch: function() {
      // console.log(v);
      // console.log(this.inputvalue);

      this.axios
        .get(`/apiv3/search?kw=${this.inputvalue}`)
        .then(res => {
          this.answer = res.data.data.result;
          console.log(res);
          this.wordsShow = false;
          this.answerShow = true;
        })
        .catch(error => {
          console.log(error);
        });
    },
    hide: function() {
      this.wordsShow = true;
      this.answerShow = false;
      this.answer = [];
    },
    goVideo:function(v){
      // console.log(v);
      this.$router.push({ name: "video", params: { videoId: v } });
      
    },
    gotoSerch:function(w){
      // console.log(w);
      this.inputvalue = w
      this.goserch()
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getheigth();
    this.getitemlist();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang="less" scoped>
//@import url(); 引入公共css类
@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot");
  src: url("../fonts/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../fonts/iconfont.woff2") format("woff2"),
    url("../fonts/iconfont.woff") format("woff"),
    url("../fonts/iconfont.ttf") format("truetype"),
    url("../fonts/iconfont.svg#iconfont") format("svg");
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.serch {
  width: 100%;
  height: 50px;
  background: #141414;
  padding: 10px;
  position: relative;
  .serchBox {
    // padding: 10px;
    background: skyblue;
    width: 90%;
    position: relative;
    border-radius: 30px;
    .serchicon {
      display: inline-block;
      position: absolute;
      left: 5px;
      font-size: 28px;
      color: #8a8a8c;
      top: 5px;
    }
    input {
      width: 100%;
      height: 35px;
      text-indent: 35px;
    }
    button {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      border: none;
      background: none;
      width: 50px;
      // font-weight: 700;
      color: #747474;
    }
  }
  .wordsBox {
    h4 {
      color: white;
      font-weight: 500;
    }
    ul {
      li {
        color: white;
        background: #252525;
        padding: 10px;
        border-radius: 5px;
        width: auto;
        display: inline-block;
        margin: 10px;
      }
    }
  }
  .answerBox {
    // background: rgb(201, 200, 200);
    // z-index: 10;
    // position: absolute;
    // top: 50px;
    // width: 85%;
    // border-radius: 10px;
    padding: 10px;
    ul {
      width: 90%;
      li {
        color: white;
        margin: 20px auto;
        border-radius: 10px;
        background: #252525;
        padding: 10px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
}
</style>
