<!--  -->
<template>
  <div class="listShow">
    <ul v-if="list.data.data">
      <li
        v-for="(item, index) in list.data.data"
        :key="index"
        @click="toShow(item.postid)"
      >
        <img :src="item.image" :alt="item.tags" />
        <div class="title">
          <h5>{{ item.cates[0].catename }}/{{ translate(item.duration) }}</h5>
          <h5>{{ item.title }}</h5>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      listId: "",
      page: 1,
      list: []
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    translate: function(time) {
      var mine;
      var second;
      console.log(second);

      mine = Math.floor(time / 60);
      second = time - mine * 60;
      return `${mine}'${second}''`;
      // console.log(time);
    },
    toShow: function(a) {
      console.log(a);
      this.$router.push({ name: "video", params: { videoId: a } });
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    console.log(this.$route.params.listId);
    this.listId = this.$route.params.listId;
    window.axios
      .get(
        `/apiv3/post/getPostInCate?p=${this.page}&size=10&cateid=${this.listId}`
      )
      .then(res => {
        this.list = res;
      })
      .catch(error => {
        console.log(error);
      });
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang="less" scoped>
//@import url(); 引入公共css类
.listShow {
  width: 100%;
  ul {
    width: 100%;
    li {
      width: 100%;
      position: relative;
      img {
        width: 100%;
      }
      .title {
        position: absolute;
        bottom: 30px;
        left: 30px;
        h5 {
          color: white;
          font-weight: 700;
        }
      }
    }
  }
}
</style>
