<!--  -->
<template>
  <div class="videoShow">
    <!-- <meta name="referrer" content="no-referrer"> -->
    <video :src="videoUrl" controls :poster="videoImg" style="width:100%"></video>
    <div class="bTitle">
      <h3>{{ thisdata.data.data.content_video[0].title }}</h3>
    </div>
    <div class="sTitle">
      <p>#{{ thisdata.data.data.cate[0] }}/{{ videoTime }}</p>
    </div>
    <div class="intro">
      <p>{{ thisdata.data.data.intro }}</p>
    </div>
    <div class="more" v-for="(item, index) in thisdata.data.data.relate_video" :key="index">
      <div class="mTitle">
        <h5>{{ item.name }}</h5>
      </div>
      <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="(item,index) in item.list" :key="index" class="fmedium" >
          <div class="medium" >
            <p >{{getTiem2(item.duration)}}</p>
            <p >{{item.title}}</p>
          </div>
          <img :src="item.image" @click="toggelShow(item.postid)"/>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: ["Id"],
  data() {
    //这里存放数据
    return {
      thisdata: {},
      videoUrl: "",
      thisId: "",
      videoImg: "",
      videoTime: ""
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    getData: function() {
      this.thisId = this.Id;
      // console.log(this.thisId);
    },
    toggelShow: function(a) {
      console.log(a);
      this.axios
      .get(`/apiv3/post/view?postid=${a}`)
      .then(res => {
        // console.log(res);
        this.thisdata = res;
        this.videoUrl = `https://api.kele8.cn/pic/${this.thisdata.data.data.content_video[0].progressive[3].https_url}`;
        this.videoImg = this.thisdata.data.data.image;
        this.getTiem(this.thisdata.data.data.duration);
      })
      .catch(error => {
        console.log(error);
      });
      // location.reload()
      // this.$router.push({ name: "video", params: { videoId: a } });
    },
    getTiem: function(time) {
      var mine;
      var second;
      console.log(second);

      mine = Math.floor(time / 60);
      second = time - mine * 60;
      this.videoTime = `${mine}'${second}''`;
    },
    getTiem2: function(time) {
      var mine;
      var second;
      console.log(second);

      mine = Math.floor(time / 60);
      second = time - mine * 60;
      return `${mine}'${second}''`;
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    
    this.getData();
    this.axios
      .get(`/apiv3/post/view?postid=${this.thisId}`)
      .then(res => {
        // console.log(res);
        this.thisdata = res;
        this.videoUrl = `https://api.kele8.cn/pic/${this.thisdata.data.data.content_video[0].progressive[3].https_url}`;
        this.videoImg = this.thisdata.data.data.image;
        this.getTiem(this.thisdata.data.data.duration);
      })
      .catch(error => {
        console.log(error);
      });
    // console.log(this.$route.meta);
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang="less" scoped>
//@import url(); 引入公共css类
.bTitle{
    width: 100%;
    margin: 0;
    // display: flex;
    // justify-content: center;
    // align-content: center;
    h3{
        color: black;
        padding: 0px 30px 0px 30px;
    }
}
.sTitle{
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 0;
    p{
        // padding: 30px;
        font-weight: 700;
    }
}
.intro{
    width: 100%;
    margin: 0;
    p{
        padding: 30px;
        font-size: 12px;
        color: gray;
        text-indent: 24px;

    }
}
.more{
    width: 100%;
    .mTitle{
        width: inherit;
        padding-left: 10px;
        font-size: 18px;
        font-weight: 700;
    }
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.medium {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: white;
  font-size: 12px;
  font-weight: 700;
  p{
      margin: 6px 0;
  }
}
img {
  z-index: 10;
  width: 100%;
  height: 100%;
}
</style>
